import React, {useEffect} from 'react'
import {Grid, Paper, Box, Typography, Icon} from "@material-ui/core";
import Aos from "aos";
import "aos/dist/aos.css";
import {makeStyles} from "@material-ui/core/styles";
import {motion} from "framer-motion";

const pathVariants = {
    hidden: {
      opacity: 0,
      pathLength: 0
    },
    visible: {
      opacity: 1,
      pathLength: 2,
      transition: {
        duration: 3,
        ease: "easeInOut",
        yoyo: Infinity,
        repeatDelay: 0.1
      }
    }
  }

// CSS STYLES
const useStyles = makeStyles(theme=> ({
    about: {
        width: "100%",
        backgroundColor: "black",
        color: "black",
        textAlign: "center",
        fontSize: "1.5em",
        paddingTop: "3.5%",
        paddingBottom: "2.5%"     
    },
    rootGrid: {
        width: "100%",
        margin: "0px"
      },
    gridLayout1: {
        backgroundColor: "black",
        borderRadius: "5px",
        paddingTop:"120px",
        padding: theme.spacing(1),
        paddingLeft: "20px"
      
       
    },
    gridLayout2: {
        backgroundColor: "black",
        border: "1px solid white",
        borderRadius: "5px",
        padding: theme.spacing(1),
        textAlign: "center"  
    }
}));

const About = () => { {
    const classes = useStyles();
    useEffect(() => {
        Aos.init({ 
            offset: 120,
            duration: 500,
            delay: 0
        });
    }, []);
    return (
        <div>
            <div style={{width: "100%", height: "1000px", background: "black"}}>
            <div style={{marginLeft:"20px", marginRight: "20px", overflowX: "hidden"}}>
                <Grid container spacing={3} className={classes.rootGrid}>  
                    <Grid item xs={12} md={6}>
                        <Paper data-aos="fade-right" className={classes.gridLayout1}>
                        
                        <Typography  style={{color: "white", fontSize:"8vw"}}>
                            Hello.
                        </Typography>
                        <Typography  style={{color: "white", fontSize:"8vw"}}>
                            I'm  <span style={{color: "#d32f2f"}}>     Sacha</span>
                        </Typography>
                   
                        </Paper>
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <Paper data-aos="fade-left" className={classes.gridLayout2}>
                        
                        </Paper>
                    </Grid>
                </Grid>
            </div>
            </div>
           
        </div>
    )
}}

export default About

